import {
    ComponentRef,
    ElementRef,
    Injectable,
    InjectionToken,
    StaticProvider,
    Type,
    ViewContainerRef
} from "@angular/core";
import {ComponentFactoryResolver} from "@angular/core";
import {ApplicationRef} from "@angular/core";
import {Injector} from "@angular/core";
import {EmbeddedViewRef} from "@angular/core";
//
import {SmpCompRefs, SmpCompData} from "../models/public";
//
import {first, Observable, timer} from "rxjs";

@Injectable()
export class SmpDomService {

    private _refs: SmpCompRefs = {};

    constructor(private _componentFactoryResolver: ComponentFactoryResolver,
                private _appRef: ApplicationRef,
                private _injector: Injector) {
    }

    /**
     *
     * @param component: the class component to dynamically append
     * @param key: a key used to retrieve the component after it's been appended (allows removal)
     * @param target: the dom selector/element/ref where the new comp will be appended
     * @param injector: optional injector to override the component params
     * @param hook: optional hook to delay the append action. Can be any observable.
     */
    appendComp<T = any>(component: Type<T>,
                        key?: string,
                        target: string | HTMLElement | ElementRef = "body",
                        injector: Injector = this._injector,
                        hook: Observable<void | number> = timer(0)): ComponentRef<T> {
        // 1. Create a component reference from the component
        const componentRef = this._componentFactoryResolver
            .resolveComponentFactory(component as Type<T>)
            .create(injector);

        // 2. Attach component to the appRef so that it's inside the ng component tree
        this._appRef.attachView(componentRef.hostView);

        // 3. Get DOM element from component
        const domElem = (componentRef.hostView as EmbeddedViewRef<any>)
            .rootNodes[0] as HTMLElement;

        if (!!key) {
            !!this._refs[key] && console.warn("A ref with this key already exists and will be overwritten");
            this._refs[key] = componentRef;
        }
        else {
            console.warn("Component appended, but key wasn't provided. It won't be possible to remove it from the DOM automagically");
        }

        hook
            .pipe(first())
            .subscribe(() => {
            // 4. Append DOM element to the body
            const targetEl = (typeof target === typeof ""
                ? document.querySelector(target as string)
                : target instanceof ElementRef ? target.nativeElement : target) || document.body;
            targetEl.appendChild(domElem);
        });

        return componentRef as ComponentRef<T>;
    }

    appendComps(compsData: SmpCompData[], injector?: Injector, hook?: Observable<void | number>): void {
        compsData.forEach((compData) => this.appendComp(compData.comp, compData.key, compData.target, injector, hook));
    }

    createInjector<T = any>(token: InjectionToken<T>, value: T, viewContainerRef?: ViewContainerRef): Injector {
        const providers: StaticProvider[] = [
            {provide: token, useValue: value}
        ];

        return Injector.create({
            parent: viewContainerRef ? viewContainerRef.injector : this._injector,
            providers
        });
    }

    removeComp(key: string): boolean {
        if (!!this._refs[key]) {
            const componentRef = this._refs[key];
            this._appRef.detachView(componentRef.hostView);
            componentRef.destroy();

            return !0;
        }
        else {
            console.warn(`No ref found with key ${key}`);

            return !1;
        }
    }

}
